.footer {
  &__wrap {
    color: rgba(var(--white-rgb), .64);
    min-height: 8rem;
    background-color: var(--black);
    text-transform: uppercase;
    justify-content: flex-start;
    align-items: center;
    @extend %flexbox !optional;
  }

  &__box {
    justify-content: flex-start;
    align-items: center;
    @extend %flexbox !optional;
  }

  &__logo {
    width: 6rem;
    object-fit: contain;

    img {
      display: block;
      width: 100%;
    }
  }

  &__copyrite {
    padding: var(--half-space) var(--full-space);
  }

  &__nav {
    ul {
      justify-content: flex-start;
      align-items: center;
      @extend %flexbox !optional;

      li {
        padding: var(--half-space) var(--full-space);
        position: relative;

        &::before {
          content: "";
          position: absolute;
          left: 0;
          top: 0;
          width: 1px;
          height: 100%;
          background-color: rgba(var(--white-rgb), .64);
        }

        a {
          font-size: 1.6rem;
          color: rgba(var(--white-rgb), .64);

          &:hover {
            color: var(--white);
          }
        }
      }
    }
  }

  &__socials {}
}